.App{
    max-width: 860px;
    margin: 40px auto;

}
button{
    background-color: transparent;
    border: 2px solid white;
    padding: 6px 12px;
    border-radius: 4px;
    color: white;
    font-weight: bold;
    cursor: pointer;
    font-size: 1em;
}

button:hover{
    background-color: #c23866;
    color: white;
}
